import {
  DetailDocumentPage,
  DocumentCreate,
  DocumentPage,
  FilterTabsPage,
  MeDocumentPage,
  MenuPages,
  SearchPages,
  TitlePages,
} from '@components/index';
import ControlPointIcon from '@mui/icons-material/ControlPoint';
import React, { useCallback, useMemo, useState } from 'react';
import { Link, Route, Routes, useLocation } from 'react-router-dom';

function DocumentWrapper() {
  const location = useLocation();
  const [valueSearch, setValueSearch] = useState('');
  const [valueFilter, setValueFilter] = useState('desc');

  const contents = {
    title: 'Bi vit',
    description:
      'Kho ti liu v bi vit c chia s, nh gi bi cng ng',
  };

  const children = (
    <Link to="create" className="btn btn-info text-white fw-bold">
      <ControlPointIcon className="me-1" />
      Vit bi
    </Link>
  );

  const [menu, setMenu] = useState({
    link1: '/documentation',
    name1: 'Bi vit',
    name2: '',
  });

  const menuTabs = useMemo(
    () => [
      { label: 'Bi vit', link: '/documentation' },
      { label: 'Bi vit ca ti', link: '/documentation/me' },
      { label: 'To bi vit', link: '/documentation/create' },
    ],
    [],
  );

  const menuFilter = useMemo(
    () => ({
      title: 'Sp xp',
      menus: [
        { label: 'Mi nht', value: 'desc' },
        { label: 'C nht', value: 'asc' },
      ],
    }),
    [],
  );

  const handleClickRoot = useCallback(() => {
    setMenu((prev) => ({ ...prev, name2: '' }));
  }, []);

  const handleClickCard = useCallback((value) => {
    setMenu((prev) => ({ ...prev, name2: value }));
  }, []);

  const handleSearch = useCallback((value) => {
    setValueSearch(value);
  }, []);

  const handleChangeFilter = useCallback((value) => {
    setValueFilter(value);
  }, []);

  return (
    <div className="d-flex flex-column flex-fill x-3 min-vh-100">
      <TitlePages contents={contents} children={children} />
      <div className="mx-4 mt-4">
        <MenuPages menu={menu} onClick={handleClickRoot} />
        <SearchPages
          className={'mt-4'}
          onSubmit={handleSearch}
          placeHolder="Tm bi vit..."
          isShowSearch={
            location.pathname === '/documentation' ||
            location.pathname === '/documentation/me'
          }
        />
        <FilterTabsPage
          menuTabs={menuTabs}
          menuFilter={menuFilter}
          onChange={handleChangeFilter}
          isShowFilter={
            location.pathname === '/documentation' ||
            location.pathname === '/documentation/me'
          }
        />
        <Routes>
          <Route
            path=""
            element={
              <DocumentPage
                valueSearch={valueSearch}
                valueFilter={valueFilter}
                onSubmit={handleClickCard}
              />
            }
          />
          <Route
            path="/me"
            element={
              <MeDocumentPage
                valueSearch={valueSearch}
                valueFilter={valueFilter}
                onSubmit={handleClickCard}
              />
            }
          />
          <Route
            path="/:id"
            element={<DetailDocumentPage onSubmit={handleClickRoot} />}
          />
          <Route path="/create" element={<DocumentCreate />} />
        </Routes>
      </div>
    </div>
  );
}

export default DocumentWrapper;
