import { constantKeys } from '@config';
import EmailIcon from '@mui/icons-material/Email';
import LockIcon from '@mui/icons-material/Lock';
import PersonIcon from '@mui/icons-material/Person';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import { useFormik } from 'formik';
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import * as Yup from 'yup';
var classNames = require('classnames');

function RegisterForm({ onSubmit }) {
  const formik = useFormik({
    initialValues: {
      email: '',
      username: '',
      password: '',
      passwordConfirm: '',
    },
    validationSchema: Yup.object({
      email: Yup.string()
        .email('nh dng email khng hp l')
        .required('Email l bt buc.'),
      username: Yup.string().required('Username l bt buc.'),
      password: Yup.string()
        .matches(
          constantKeys.REGEX_PASSWORD,
          'S dng 8 k t tr ln vi s kt hp ca cc ch ci v s.',
        )
        .required('Mt khu l bt buc.'),
      passwordConfirm: Yup.string()
        .matches(
          constantKeys.REGEX_PASSWORD,
          'S dng 8 k t tr ln vi s kt hp ca cc ch ci v s.',
        )
        .oneOf([Yup.ref('password'), null], 'Mt khu khng khp')
        .required('Nhp li mt khu l bt buc.'),
    }),
    onSubmit: async (values) => {
      if (onSubmit) {
        await onSubmit(values);
      }
    },
  });

  const [isVisibility, setIsVisibility] = useState({
    password: false,
    confirmPassword: false,
  });

  return (
    <div className="container">
      <div className="row justify-content-center">
        <div className="col-md-6">
          <div className="text-center mt-5">
            <Link to="/">
              <img
                className="opacity-75"
                height={75}
                src={require('../../../assets/img/ITCenterBlack.png')}
              />
            </Link>
          </div>
          <div className="card mt-4 mb-5">
            <div className="card-body">
              <form onSubmit={formik.handleSubmit}>
                <div className="form-group mt-2">
                  <label htmlFor="email" className="fs-5 text-dark mb-2">
                    Email:
                  </label>
                  <div
                    className={classNames('input-group h-50px', {
                      'is-valid': formik.errors.email && formik.touched.email,
                    })}>
                    <span className="input-group-text">
                      <EmailIcon />
                    </span>
                    <input
                      name="email"
                      type="email"
                      className="form-control fs-5"
                      placeholder="Vui lng nhp email"
                      value={formik.values.email}
                      onChange={formik.handleChange}
                    />
                  </div>
                  {formik.errors.email && formik.touched.email && (
                    <div className="mt-2 text-danger">
                      {formik.errors.email}
                    </div>
                  )}
                </div>
                <div className="form-group mt-2">
                  <label htmlFor="username" className="fs-5 text-dark mb-2">
                    Username:
                  </label>
                  <div
                    className={classNames('input-group h-50px', {
                      'is-valid':
                        formik.errors.username && formik.touched.username,
                    })}>
                    <span className="input-group-text">
                      <PersonIcon />
                    </span>
                    <input
                      name="username"
                      type="text"
                      className="form-control fs-5"
                      placeholder="Vui lng nhp username"
                      value={formik.values.username}
                      onChange={formik.handleChange}
                    />
                  </div>
                  {formik.errors.username && formik.touched.username && (
                    <div className="mt-2 text-danger">
                      {formik.errors.username}
                    </div>
                  )}
                </div>
                <div className="form-group mt-3">
                  <label htmlFor="password" className="fs-5 text-dark mb-2">
                    Mt Khu:
                  </label>
                  <div
                    className={classNames('input-group h-50px', {
                      'is-valid':
                        formik.errors.password && formik.touched.password,
                    })}>
                    <span className="input-group-text">
                      <LockIcon />
                    </span>
                    <input
                      name="password"
                      type={isVisibility.password ? 'text' : 'password'}
                      className="form-control border-end-0 fs-5"
                      placeholder="Vui lng nhp mt khu"
                      value={formik.values.password}
                      onChange={formik.handleChange}
                    />
                    <span
                      className="input-group-text bg-transparent cursor-pointer"
                      onClick={() =>
                        setIsVisibility({
                          ...isVisibility,
                          password: !isVisibility.password,
                        })
                      }>
                      {isVisibility.password ? (
                        <VisibilityIcon />
                      ) : (
                        <VisibilityOffIcon />
                      )}
                    </span>
                  </div>
                  {formik.errors.password && formik.touched.password && (
                    <div className="mt-2 text-danger">
                      {formik.errors.password}
                    </div>
                  )}
                </div>
                <div className="form-group mt-3">
                  <label
                    htmlFor="passwordConfirm"
                    className="fs-5 text-dark mb-2">
                    Nhp Li Mt Khu:
                  </label>
                  <div
                    className={classNames('input-group h-50px', {
                      'is-valid':
                        formik.errors.passwordConfirm &&
                        formik.touched.passwordConfirm,
                    })}>
                    <span className="input-group-text">
                      <LockIcon />
                    </span>
                    <input
                      name="passwordConfirm"
                      type={isVisibility.passwordConfirm ? 'text' : 'password'}
                      className="form-control border-end-0 fs-5"
                      placeholder="Vui lng nhp li mt khu"
                      value={formik.values.passwordConfirm}
                      onChange={formik.handleChange}
                    />
                    <span
                      className="input-group-text bg-transparent cursor-pointer"
                      onClick={() =>
                        setIsVisibility({
                          ...isVisibility,
                          passwordConfirm: !isVisibility.passwordConfirm,
                        })
                      }>
                      {isVisibility.passwordConfirm ? (
                        <VisibilityIcon />
                      ) : (
                        <VisibilityOffIcon />
                      )}
                    </span>
                  </div>
                  {formik.errors.passwordConfirm &&
                    formik.touched.passwordConfirm && (
                      <div className="mt-2 text-danger">
                        {formik.errors.passwordConfirm}
                      </div>
                    )}
                </div>
                <div className="mt-3  d-flex align-items-center fs-5">
                  Khi ng k, bn  ng  vi iu khon s dng v Chnh
                  sch bo mt ca chng ti.
                </div>
                <div className="d-grid mt-3">
                  <button
                    className="btn btn-info text-white btn-block py-2 fs-5 fw-bold"
                    type="submit">
                    ng K
                  </button>
                </div>
                <p className="text-center mt-3">
                  Bn  c ti khon?{' '}
                  <Link
                    to="/auth/login"
                    className="text-info text-decoration-none">
                    ng Nhp!
                  </Link>
                </p>
                <p className="text-center m-0">
                  <Link
                    to="/auth/forgot-password"
                    className="text-info text-decoration-none">
                    Qun mt khu?
                  </Link>
                </p>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default RegisterForm;
