import {
  AboutPage,
  ContactPage,
  DonatePage,
  SupporterPage,
  TabsPage,
  TitlePages,
} from '@components/index';
import React, { useMemo } from 'react';
import { Route, Routes } from 'react-router-dom';

function AboutWrapper() {
  const contents = {
    title: 'V ITcenter',
    description:
      'V mt nn gio dc min ph cho bt c ai,  bt c ni no.',
  };

  const menuTabs = useMemo(
    () => [
      { label: 'V ITCenter', link: '/about' },
      { label: 'Ngi ng h', link: '/about/supporters' },
      { label: 'Lin h - Gp ', link: '/about/contact' },
      { label: 'Ti tr', link: '/about/donate' },
    ],
    [],
  );

  return (
    <div className="d-flex flex-column flex-fill x-3 min-vh-100">
      <TitlePages contents={contents} isShowAction={false} />
      <TabsPage menuTabs={menuTabs} className={'justify-content-center m-0'} />
      <Routes>
        <Route path="" element={<AboutPage />} />
        <Route path="/supporters" element={<SupporterPage />} />
        <Route path="/contact" element={<ContactPage />} />
        <Route path="/donate" element={<DonatePage />} />
      </Routes>
    </div>
  );
}

export default AboutWrapper;
